'use client'
export default function NotFound() {
    return (
        <div
            className="max-w-7xl mx-auto px-6 py-16 flex flex-col md:flex-row items-center justify-between min-h-[320px] relative overflow-visible">
            <div className="max-w-lg md:max-w-md lg:max-w-lg">
                <h1 className="text-[#152B3B] font-extrabold text-3xl md:text-4xl leading-tight mb-2">
                    Ooops...
                </h1>
                <h2 className="text-[#152B3B] font-normal text-2xl md:text-3xl leading-tight mb-6">
                    Page not found
                </h2>
                <p className="text-[#152B3B] text-sm md:text-base mb-8 max-w-xs">
                    A massa, interdum pretium, ut sit est nec. Convallis fames proin lacus cras.
                </p>
                <button aria-label="Go Back"
                        className="bg-[#F75C5C] text-white font-semibold rounded-md px-6 py-3 flex items-center gap-2 hover:bg-[#e04e4e] transition-colors">
                    Go Back
                    <svg aria-hidden="true" className="h-4 w-4" fill="none" stroke="currentColor" stroke-width="2"
                         viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                        <path d="M9 5l7 7-7 7" stroke-linecap="round" stroke-linejoin="round">
                        </path>
                    </svg>
                </button>
            </div>
            <div className="relative mt-10 md:mt-0 flex flex-col items-center">
                <h3 className="text-[#152B3B] font-extrabold text-5xl md:text-6xl leading-none mb-1 select-none">
                    404
                </h3>
                <p className="text-[#152B3B] font-normal text-base md:text-lg select-none">
                    Page not found
                </p>
                <img
                    alt="Illustration of two people floating, one holding a red plug and the other holding a red socket, with red cable connecting them and pink abstract shapes behind"
                    className="mt-6 w-[400px] max-w-full" decoding="async" height="280" loading="lazy"
                    src="https://storage.googleapis.com/a1aa/image/69c5f5d2-d30f-44aa-de5a-a9a1ea98b93c.jpg"
                    width="400"/>
            </div>
        </div>
    );
}
